.video-container {
  width: 60%;
  padding: 3em;

  & > div {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 1.5em;
    justify-items: center;
    align-items: center;
    width: fit-content;
    margin: auto;
    column-gap: 4em;
  }

  & video {
    max-width: 100%;
    border-radius: 12px;
    grid-column: 1 / -1;
  }
}

@media (max-width: 62.5em) {
  .video-container{
    width: 50%;
  }
  
}

@media (max-width: 48.9375em) {
  .video-container > div{
    column-gap: 2em;

  }
  
}
